import React from 'react'
import { graphql, Link } from 'gatsby'
import { GatsbyImage, getImage } from 'gatsby-plugin-image'
import Seo from '../components/seo'

const IllustrationsPage = ({data}) => {
  const { allMdx: { nodes: illustration }} = data
  return (
    <section className='grid grid-cols-1 gap-8 lg:grid-cols-1'>
      {illustration.map((item) => {
        const { id, frontmatter: {thumb, title, subtitle, slug} } = item
        return <article key={id} className='h-96 lg:h-144 min-h-full aspect-w-1 aspect-h-1 w-full overflow-hidden bg-black relative'>
          <Link to={`/illustrations/${slug}`}>
            <GatsbyImage image={getImage(thumb)} alt={title} objectFit='cover' className='thumbnail h-full hover:scale-105 transition duration-500 ease-in-out absolute hover:opacity-40' />
            <div className='thumbnail-hover-detail absolute top-0 w-full h-full pointer-events-none transition duration-500 ease-in-out'>
              <div className='flex flex-col h-full justify-center'>
                <h1 className='font-bold text-4xl lg:text-8xl text-white text-center tracking-wider'>{title}</h1>
                <h3 className='font-regular text-xl lg:text-3xl text-white text-center mt-4 lg:mt-8 tracking-wider'>{subtitle}</h3>
              </div>
            </div>
          </Link>
        </article>
      })}
    </section>
  )
}

export const query = graphql`
  query {
    allMdx(sort: {frontmatter: {date: DESC}}) {
      nodes {
        id
        frontmatter {
          title
          subtitle
          thumb {
            childImageSharp {
              gatsbyImageData
            }
          }
          author
          slug
          date
        }
      }
    }
  }
`

export const Head = () => <Seo title="Illustrations" />

export default IllustrationsPage
